import React, { useEffect, useContext, useState } from "react";
import "./dashboard.css";
import { FaUsers, FaDonate, FaBox } from "react-icons/fa";
import { MainContext } from "../Helpers/Context";
import Box from "../../small_comps/box/Box";
const Dashboard = () => {
  // declaring state variables
  let { loggedSchool, url } = useContext(MainContext); // variables shared among all the files from main.jsx
  const [totalStudent, setTotalStudent] = useState("");
  const [totalMonth, setTotalMonth] = useState(0);
  const [totalYear, setTotalYear] = useState(0);
  const [Month, setMonth] = useState("");
  const [Year, setYear] = useState("");
  useEffect(() => {
    // fetch details of dashboar usng async function before the page loads
    fetch(`${url}/main/dashboard`, {
      credentials: "include",
      headers: {
        "Content-Type": "application/json",
      },
      method: "GET",
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.status) {
          // check is status is true

          // set variables with the data gotten if only status is true

          setTotalStudent(data.message);
          setMonth(data.month[0]?.month_payment);
          setYear(data.year[0]?.year_payment);
          setTotalMonth(data.month[0]?.total);
          setTotalYear(data.year[0]?.total);
        }
      })
      .catch((err) => console.log(err));
  }, [loggedSchool, url]);
  return (
    <div className="__dashboard">
      <h2>Dashboard</h2>
      <p>Welcome to {loggedSchool} School Fee Record System</p>
      <br />
      <div className="container">
        <Box style={{ backgroundColor: "#eee" }}>
          <div className="content">
            <h3>Students</h3>
            <FaUsers size={70} />
            <p>
              <b>{totalStudent || "0"} </b>
            </p>
          </div>
        </Box>
        <Box style={{ backgroundColor: "wheat" }}>
          <div className="content" title={Month}>
            <h3>Monthly Fee</h3>
            <FaDonate size={60} />
            <p>
              <b>{totalMonth?.toLocaleString() || "0"}</b>
            </p>
          </div>
        </Box>
        <Box style={{ backgroundColor: "#c5d7e5" }}>
          <div className="content" title={Year}>
            <h3>Total </h3>
            <FaBox size={60} />
            <p>
              <b>{totalYear?.toLocaleString() || "0"}</b>
            </p>
          </div>
        </Box>
      </div>
    </div>
  );
};

export default Dashboard;
